import React, { createContext, useContext, useState, useEffect } from "react";
import { auth } from "../auth/firebase";
import {
  GoogleAuthProvider,
  createUserWithEmailAndPassword,
  onAuthStateChanged,
  sendPasswordResetEmail,
  signInWithEmailAndPassword,
  signInWithPopup,
  signOut,
  updateProfile,
} from "firebase/auth";
import { useNavigate } from "react-router-dom";
import {
  toastErrorNotify,
  toastSuccessNotify,
  toastWarnNotify,
} from "../helpers/ToastNotify";
const AuthContext = createContext();

export const useAuthContext = () => {
  return useContext(AuthContext);
};

const AuthProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(
    JSON.parse(sessionStorage.getItem("currentUser")) || null
  );
  const navigate = useNavigate();

  useEffect(() => {
    userObserver();
  }, []);

  const createUser = async (email, password, displayName) => {
    //? ifre 6 karakterden az ise kullancya uyar
    if (password.length < 6) {
      toastWarnNotify("Password should be at least 6 characters");
      return;
    }
    //?createUser metodunu ardmz yerden email, password alnr, createUser' register sayfasnda handleSubmit fonksiyonu iinde kullanacaz.
    try {
      //!Yeni bir kullanc oluturmakiin kullanlan firebase metodu
      //? auth, url gibi; email ve password ise data gibi
      //? createUserWithEmailAndPassword metodu post ilemi gibi
      const userCredential = await createUserWithEmailAndPassword(
        //!auth metodu firebase.js den import
        auth,
        email,
        password
      );
      console.log(userCredential);

      //!Kullanc bilgilerini gncellemek iin register iinde bu firebase metodunu kullanyoruz, displayName register alnacak. nk ilk anda displayName bo olacaktr ve kullancnn ismini ekranda grmesi iin en gncel bilgiler bu metodla salanacaktr.
      await updateProfile(auth.currentUser, {
        displayName: displayName,
      });
      navigate("/login"); //? Kullanc kayt olduktan sonra login sayfasna yonlendirmek iin
      toastSuccessNotify("Registration successful");
      console.log(userCredential);
    } catch (error) {
      toastErrorNotify(error.message); //?firebase'den gelen error ierisindeki message bilgisini toastErrorNotify ile kullancya uyar olarak veriyoruz
    }
  };

  //!email ve password ile girii enable yapyoruz
  const signIn = async (email, password) => {
    //?signIn metodunu ardmz yerden email, password alnr, signIn'i login sayfasnda handleSubmit fonksiyonu iinde kullanacaz.
    try {
      //!Mevcut kullancnn giri yapmas iin kullanlan firebase metodu
      //? auth, url gibi; email ve password ise data gibi
      const userCredential = await signInWithEmailAndPassword(
        //!auth metodu firebase.js den import edilir
        auth,
        email,
        password
      );
      navigate("/"); //? Kullanc giri yaptktan sonra login sayfasna yonlendirmek iin
      toastSuccessNotify("Login successful");
      console.log(userCredential);
    } catch (error) {
      console.log(error);
    }
  };

  const logOut = () => {
    signOut(auth)
      .then(() => {
        // Sign-out successful.
        toastSuccessNotify("Logout Successful");
      })
      .catch((error) => {
        // An error happened.
        toastErrorNotify("An error happened", error);
      });
  };

  //!Bir kullanc baaryla oturum atnda gzlemcide kullanc hakknda bilgi alabilmek iin kullanlan firebase metodu.(onAuthStateChanged). Bu ilemi tek sefer yapmas yeterlidir
  const userObserver = () => {
    //? userObserver fonksiyonu, onAuthStateChanged ile otomatik takip yapaca iin artk dier fonksiyonlarda ve metodlarda setCurrentUser armaya gerek kalmad.

    onAuthStateChanged(auth, (user) => {
      //!displayName kullanc bilgilerini alabilmek iin bu kodlardan sonra updateProfile metoduna ihtiyacmz var
      if (user) {
        //?user login olduunda
        const { email, displayName, photoURL } = user;
        setCurrentUser({ email, displayName, photoURL });
        sessionStorage.setItem(
          "currentUser",
          JSON.stringify({ email, displayName, photoURL })
        );
      } else {
        //?user logout olduunda
        setCurrentUser(null);
      }
    });
  };

  //? 1.Google ile girii enable ediyoruz
  //? 2.Projeyi deploy ettikten sonra google sign-in almas iin domain listesine deploy linkini ekle(Authentication => settings => Authorized domains => add domain)
  //* googleProvider fonksiyonu register ve login de kullanlacak
  const googleProvider = () => {
    const provider = new GoogleAuthProvider();

    //!Alr pencereyle oturum amak iin signInWithPopup metodu
    signInWithPopup(auth, provider)
      .then((result) => {
        navigate("/");
        toastSuccessNotify("Login Successful");
      })
      .catch((error) => {
        toastErrorNotify(error.message);
      });
  };

  //?sendPasswordResetEmail metodunu kullanarak bir kullancya ifre sfrlama e-postas gnderebilmek iin
  const forgotPassword = (email) => {
    //? Kullanc ifresini sfrlayabilmesi iin email girmek zorundadr.
    sendPasswordResetEmail(auth, email)
      .then(() => {
        toastWarnNotify("Password reset email sent to your email box!");
      })
      .catch((error) => {
        toastErrorNotify("Enter your email for reset",error.message);
      });
  };

  // console.log(currentUser);

  const values = {
    currentUser,
    createUser,
    signIn,
    logOut,
    googleProvider,
    forgotPassword,
  };
  return <AuthContext.Provider value={values}>{children}</AuthContext.Provider>;
};

export default AuthProvider;
